.product-card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
}

.product-card__img {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.product-card__title {
  color: #060b35;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.product-card__sku {
  color: #616070;
  margin-bottom: 16px;
  line-height: 14px;
}

.product-card__rating {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  display: flex;
}

.product-card__reviews {
  color: #060b35;
  line-height: 14px;
}

.product-card__pricing {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  line-height: 18px;
  display: flex;
}

.product-card__pricing-label {
  color: #616070;
  font-size: 12px;
}

.product-card__pricing-amount {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
}

.product-card__button {
  box-sizing: border-box;
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 40px;
  text-decoration: none;
  display: block;
}

.product-card__button:hover {
  color: #00acdc;
  background-color: #fff;
}

.stars {
  display: flex;
}

.stars__star {
  background: url("star.822d561e.svg") center no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star:not(:last-child) {
  margin-right: 4px;
}

.stars--1 .stars__star:first-child, .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

body {
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-weight: 400;
}

p {
  margin: 0;
}
